<template>
  <div class="goodsDetail">
    <div class="mescroll">
      <div class="banner">
        <van-swipe @change="onChange" :show-indicators="false">
          <van-swipe-item v-for="(item,index) in goodsDetail.pics" :key="index">
            <img :src="$utils.dealImg(item)" alt />
          </van-swipe-item>
        </van-swipe>
        <div
          class="custom-indicator"
        >{{ current + 1 }}/{{goodsDetail.pics?goodsDetail.pics.length:''}}</div>
      </div>
      <div class="info">
        <div class="title">
          <p>{{goodsDetail.gname}}</p>
          <img src="../../assets/img/icon/share.png" alt @click="share" />
        </div>
        <div>
          <p class="text"> {{goodsDetail.gdes}}</p>
          <!-- <div class="labels">
            <div class="label color">送 
            <span>{{goodsDetail.gfanj}}</span>
            分
            </div>
          </div>  -->
        </div>
        <!-- <div class="labels">
          <div class="label">预计24小时发货</div>
          <div class="label">预计发货后3天送达</div>
        </div> -->
      </div>
      <div v-html="goodsDetail.gmsg" class="content">{{goodsDetail.gmsg }}</div>
    </div>
    <div class="bottom">
      <div class="left">
        <p class="price" v-if="true">
          <span class="font">¥</span>
          {{goodsDetail.goutPrice?goodsDetail.goutPrice.toFixed(2):'0.00'}}

          <span class="text">¥ {{goodsDetail.gmarketPrice?goodsDetail.gmarketPrice.toFixed(2):'0.00'}}</span>
        </p> 
        <!-- <p class="text" v-if="false">已优惠¥10.00</p> -->
      </div>
      <!-- <div class="add" @click="type = 1,show = true">加入购物车</div> -->
      <div class="buy" @click="type = 2,show = true">立即购买</div>
      <!-- <div class="buy" @click="promote">立即推广</div> -->
    </div>

    <button
      style="display: none"
      ref="btn"
      class="copy-btn"
      data-clipboard-action="copy"
      data-clipboard-target="#telno"
      @click="copy"
    ></button>
    <div class="canvas">
      <canvas id="stockGraph"></canvas>
    </div>
    <div class="mask" v-show="shareShow" @click="shareShow = false">
      <img class="pic" id="avatar" alt />
    </div>
    <van-popup v-model="show" position="bottom">
      <div class="popup">
        <div class="icon" @click="show = false">×</div>
        <div class="popup-top">
          <img :src="goodsDetail.pics?$utils.dealImg(goodsDetail.pics[0]):''" alt />
          <div>
            <div class="price">
              <span class="label">市场价</span>
              <span class="font">¥</span>
              <span class="num">
                <del>{{goodsDetail.gmarketPrice?goodsDetail.gmarketPrice.toFixed(2):'0.00'}}</del>
              </span>
            </div>
            <div class="price1">
              <span class="label">今日售价</span>
              <span class="font">¥</span>
              <span class="num">{{goodsDetail.goutPrice?goodsDetail.goutPrice.toFixed(2):'0.00'}}</span>
            </div>
            <div class="inventory" v-if="false">库存：69件</div>
          </div>
        </div>
        <div class="popup-mid">
          <div class="num">
            <p class="title">数量</p>
            <van-stepper v-model="num" @blur="blur" />
          </div>
        </div>
        <div class="popup-btn" @click="submit">{{type == 1?'加入购物车':'立即购买'}}</div>
      </div>
    </van-popup>
    <van-dialog
      v-model="dialogShow"
      title="推广"
      show-cancel-button
      confirm-button-text="分享"
      cancel-button-text="复制"
      @confirm="dialogConfirm"
      @cancel="dialogCancel"
    >
      <div class="dialog">
        <img :src="QRcode" />
        <div
          id="telno"
          class="text"
        >{{ $config.http + "/wx/#/cardThree?id=" + $route.query.id + "&parentId=" + userId + "&title=" + $route.query.title}}</div>
      </div>
    </van-dialog>
    <div class="mask1" v-if="shareShow1" @click="hideShareMask">
      <img src="../../assets/img/activity/shang.png" alt />
    </div>
  </div>
</template>
<script>
import { Swipe, SwipeItem, Popup, Stepper, Dialog } from "vant";
import { getGoodsDetail, addShoppingCart, getPersonalInfo, wxApi } from "@/api"; 
import { mapActions } from "vuex";
import ClipboardJS from "clipboard";
export default {
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Popup.name]: Popup,
    [Stepper.name]: Stepper,
    [Dialog.name]: Dialog,
  },
  data() {
    return {
      current: 0,
      show: false,
      num: 1,
      type: 2,
      shareShow: false,
      goodsDetail: {},
      QRcode: "",
      sharePic: "",
      canvasHight: 0,
      userId: "",
      dialogShow: false,
      shareShow1: false,
      userLevel: 1,
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    promote() {
      //进行判断，如果还不是代理商，则引导去注册；如果已是代理商，则弹出对话框
      if(this.userLevel > 1)
      {
        this.dialogShow = true;
      }
      else{
         this.$toast("请先注册成为代理商");
         this.$router.push({path: '/agentRegistration'});
      }
      
      // let that = this;
      // Dialog.confirm({
      //   title: "推广",
      //   message:
      //     that.$config.http +
      //     "/wx/#/cardThree?id=" +
      //     that.$route.query.id +
      //     "&parentId=" +
      //     sessionStorage.getItem("userId")
      // + "&title=" + that.$route.query.title,
      //   confirmButtonText: "复制",
      // })
      //   .then(() => {
      //     // on confirm
      //     that.$refs.btn.click();
      //   })
      //   .catch(() => {
      //     // on cancel
      //   });
    },
    dialogConfirm() {
      this.shareShow1 = true;
    },
    hideShareMask() {
      this.shareShow1 = false;
    },
    dialogCancel() {
      this.$refs.btn.click();
    },
    copy() {
      let that = this;
      var clipboard = new ClipboardJS(".copy-btn");
      clipboard.on("success", (e) => {
        that.$toast("复制成功");
        clipboard.destroy();
      });
      clipboard.on("error", (e) => {
        that.$toast("复制失败");
        clipboard.destroy();
      });
    },
    onChange(index) {
      this.current = index;
    },
    share() {
      this.shareShow = true;
      this.creatCanvas();
    },
    creatCanvas() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      let that = this;
      var wid1 = 900 * 0.29; //大 右
      var hei1 = 1100 * 0.37; //大 下
      var canvas = document.getElementById("stockGraph");
      var ctx = canvas.getContext("2d");
      var ratio = this.getPixelRatio(ctx);
      canvas.width = 750 * ratio;
      canvas.height = 1100 * ratio;
      ctx.rect(0, 0, canvas.width * ratio, canvas.height * ratio);
      ctx.fillStyle = "#fff";
      ctx.fill();
      var img = new Image();
      img.src = that.$utils.dealImg(that.sharePic);
      img.setAttribute("crossOrigin", true);
      img.onload = function () {
        ctx.drawImage(img, 0, 0, canvas.width, canvas.width);
        ctx.fillStyle = "#000";
        ctx.font = "5em bold 黑体";
        that.drawtext(
          ctx,
          that.goodsDetail.gname,
          50,
          canvas.width + 150,
          canvas.width - 50 - 50 - 250 * ratio
        );
        // ctx.fillText("白色款", 50, that.canvasHight);
        ctx.fillStyle = "#f00";
        ctx.font = "4em normal 黑体";
        ctx.fillText("¥", 50, canvas.width + 250 * ratio);
        ctx.font = "5em bold 黑体";
        ctx.fillText(
          that.goodsDetail.goutPrice.toFixed(2),
          100,
          canvas.width + 250 * ratio
        );
        ctx.fillStyle = "#999";
        ctx.font = "4em bold 黑体";
        ctx.textAlign = "center";
        ctx.fillText(
          "截屏分享，长按扫描二维码购买",
          canvas.width / 2,
          canvas.height - (canvas.height - canvas.width - 250 * ratio - 40) / 2
        );
        var img2 = new Image();
        img2.src = that.QRcode;
        img2.onload = function () {
          ctx.drawImage(
            img2,
            canvas.width - 250 * ratio - 40,
            canvas.width + 40,
            250 * ratio,
            250 * ratio
          );
          var base64 = canvas.toDataURL(); //"image/png" 这里注意一下,参数里面不要带"image/png" 之前网上代码还有这个参数一直报错
          var avatar = document.getElementById("avatar");
          avatar.setAttribute("src", base64);
          loading.clear();
        };
      };
    },
    drawtext(ctx, t, x, y, w) {
      // 文字换行
      //参数说明
      //ctx：canvas的 2d 对象，t：绘制的文字，x,y:文字坐标，w：文字最大宽度
      let chr = t.split("");
      let temp = "";
      let row = [];
      for (let a = 0; a < chr.length; a++) {
        if (
          ctx.measureText(temp).width < w &&
          ctx.measureText(temp + chr[a]).width <= w
        ) {
          temp += chr[a];
        } else {
          row.push(temp);
          temp = chr[a];
        }
      }
      row.push(temp);
      for (let b = 0; b < row.length; b++) {
        ctx.fillText(row[b], x, y + b * 100); //每行字体y坐标间隔100
      }
      this.canvasHight = y + row.length * 100;
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    submit() {
      if (this.type == 1) {
        const loading = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true, // 禁用背景点击
        });
        addShoppingCart({
          gcode: this.goodsDetail.gcode,
          gorderCount: this.num,
        }).then((res) => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("加入成功");
            this.show = false;
          } else {
            this.$toast(res.msg);
          }
        });
      } else {
        let data = [
          {
            id: this.goodsDetail.gcode,
            gname: this.goodsDetail.gname,
            gimg: this.goodsDetail.gimg,
            goutPrice: this.goodsDetail.goutPrice,
            num: this.num,
          },
        ];
        sessionStorage.setItem("goodsList", JSON.stringify(data));
        this.$router.push({
          name: "confirmOrder",
          query: {
            type: "1",
          },
        });
      }
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getGoodsDetail({
        gcode: this.$route.query.id,
        shopUrl:
          this.$config.http +
          "/wx/#/goodsDetail?id=" +
          this.$route.query.id +
          "&parentId=" +
          sessionStorage.getItem("userId") +
          "&title=" +
          this.$route.query.title,
      }).then((res) => {
        loading.clear();
        if (res.code == 0) {
          this.goodsDetail = res.data;
          this.QRcode = res.data.qrCode;
          this.goodsDetail.pics = this.goodsDetail.gimg.split(",");
          this.sharePic = this.goodsDetail.pics[0];
          // this.goodsDetail.gmsg = this.goodsDetail.gmsg.replace(
          //   /(<img[\s\S]+?)src=(['^http"])/g,
          //   '<img src="' + this.$config.http
          // );
        } else {
          this.$toast(res.msg);
        }
      });
       getPersonalInfo().then(res => {
        loading.clear();
        if (res.code == 0) { 
          this.userLevel = parseInt(res.data.userLevel);
          sessionStorage.setItem("userCode", res.data.userCode);
          this.$toast(res.data.userLevel);
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
    share() {
      let that = this;
      wxApi()
        .then((res) => {
          console.log(this.$wx, res.data);
          this.$wx.config({
            debug: false,
            appId: res.data.appId,
            timestamp: res.data.timestamp,
            nonceStr: res.data.nonceStr,
            signature: res.data.signature,
            jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"],
          });
          this.$wx.ready(function () {
            that.$wx.onMenuShareAppMessage({
              title: that.goodsDetail.gname, // 分享标题
              desc: that.goodsDetail.gname, // 分享描述
              link:
                that.$config.http +
                "/wx/#/goodsDetail?id=" +
                that.$route.query.id +
                "&parentId=" +
                sessionStorage.getItem("userId") +
                "&title=" +
                that.$route.query.title, // 分享链接
              imgUrl: that.$utils.dealImg(that.goodsDetail.pics[0]), // 分享图标
              type: "link", // 分享类型,music、video或link，不填默认为link
              dataUrl: "", // 如果type是music或video，则要提供数据链接，默认为空
              success: function () {
                // 用户确认分享后执行的回调函数
                that.$toast("分享成功");
              },
              cancel: function () {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
            that.$wx.onMenuShareTimeline({
              title: that.goodsDetail.gname, // 分享标题
              link:
                that.$config.http +
                "/wx/#/goodsDetail?id=" +
                that.$route.query.id +
                "&parentId=" +
                sessionStorage.getItem("userId") +
                "&title=" +
                that.$route.query.title, // 分享链接
              imgUrl: that.$utils.dealImg(that.goodsDetail.pics[0]), // 分享图标
              success: function () {
                that.$toast("分享成功");
              },
              cancel: function () {
                // 用户取消分享后执行的回调函数
                that.$toast.fail("取消分享");
              },
            });
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    document.title = "商品详情";
    this.userId = sessionStorage.getItem("userId");
    sessionStorage.removeItem("address");
    sessionStorage.removeItem("goodsList");
    sessionStorage.removeItem("couponDetail");
    this.loadData();
    this.share();
    if (this.$route.query.buy == "1") {
      this.show = true;
    }
  },
};
</script>
<style lang="scss" scoped>
.goodsDetail {
  height: 100vh;
  .banner {
    position: relative;
    .van-swipe-item {
      img {
        width: 100%;
      }
    }
  }
  .info {
    padding: 0 10px;
    .title {
      display: flex;
      align-items: center;
      color: goldenrod;
      p {
        font-weight: bold;
        flex: 2;
        line-height: 25px;
      }
      img {
        width: 20px;
        height: 20px;
      }
    }
    .redPacket {
      font-size: 14px;
      line-height: 25px;
      flex: 2;
      span {
        color: #fe6263;
      }
    }
    .text {
      font-size: 14px;
      line-height: 25px;
      color: goldenrod;
    }
    .labels {
      margin: 10px 0 0;
      .label {
        display: inline-block;
        font-size: 13px;
        margin-right: 10px;
        background: #eee;
        padding: 3px 8px;
        border-radius: 10px;
      }
      .color {
        background: #ffeff2;
        color: #ef5852;
      }
    }
  }

  .bottom {
    background: #fff;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    width: calc(100% - 20px);
    height: 60px;
    padding: 0 10px;
    .left {
      flex: 2;
      .price {
        font-size: 18px;
        font-weight: bold;
        .font {
          font-weight: normal;
          font-size: 13px;
          margin-right: 5px;
        }
      }
      .text {
        font-size: 13px;
        color: #999;
      }
    }
    .add {
      border: 1pt solid #ef5852;
      color: #ef5852;
      width: 90px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 14px;
      margin-right: 10px;
      border-radius: 5px;
    }
    .buy {
      border: 1pt solid #ef5852;
      color: #fff;
      background: #ef5852;
      width: 90px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      font-size: 14px;
      border-radius: 5px;
    }
  }
  .canvas {
    canvas {
      width: 280px;
      height: 400px;
      display: none;
    }
  }
  .mask {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 1;
    img {
      width: 280px;
      height: 400px;
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      border-radius: 5px;
      z-index: 2;
      border: 0;
    }
  }
  .popup {
    position: relative;
    background: #fff;
    .icon {
      position: absolute;
      right: 15px;
      top: 10px;
      width: 10px;
      height: 10px;
      color: #999;
      font-size: 20px;
    }
    &-top {
      display: flex;
      padding: 10px;
      img {
        width: 120px;
        height: 120px;
        margin-right: 10px;
      }
      .price {
        padding: 5px 0;
        .label {
          font-size: 14px;
          color: #c3baac;
          background: #333;
          padding: 3px 5px;
          border-radius: 5px;
          margin-right: 10px;
          box-sizing: border-box;
          border: 1pt solid #333;
        }
        .font {
          font-size: 12px;
          color: #dbd0c1;
          margin-right: 3px;
        }
        .num {
          font-weight: bold;
          font-size: 16px;
          color: #dbd0c1;
        }
      }
      .price1 {
        padding: 5px 0;
        .label {
          font-size: 14px;
          color: #ef5852;
          border: 1pt solid #ef5852;
          padding: 3px 5px;
          border-radius: 5px;
          margin-right: 10px;
          box-sizing: border-box;
        }
        .font {
          font-size: 12px;
          color: #ef5852;
          margin-right: 3px;
        }
        .num {
          font-weight: bold;
          font-size: 16px;
          color: #ef5852;
        }
      }
      .inventory {
        margin-top: 10px;
        color: #999;
        font-size: 13px;
      }
    }
    &-mid {
      padding: 10px;
      .num {
        margin: 20px 0 0;
        display: flex;
        .title {
          flex: 2;
        }
      }
    }
    &-btn {
      margin: 10px 0 0;
      background: #ef5852;
      height: 60px;
      color: #fff;
      line-height: 60px;
      text-align: center;
    }
  }
  .dialog {
    text-align: center;
    img {
      width: 80%;
    }
    .text {
      padding: 5px 20px;
      word-wrap: break-word;
    }
  }
  .mask1 {
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    img {
      position: fixed;
      top: 10px;
      right: 10px;
      width: 100px;
    }
  }
}
</style>
<style lang="scss">
.goodsDetail {
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 2px 10px;
    border-radius: 10px;
    color: #fff;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
  }
  .content {
    margin: 20px 0 0;
    padding: 0 0 70px;
    img {
      width: 100% !important;
      display: block;
    }
  }
}
</style>